import React, { useState } from 'react';
import { Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import { BasicDD } from '../../components/customDropdown';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import { optionOverviewFilter } from '../../constants/filterOptions';
import { convertToPercent } from '../../constants/formatter';

const OverviewContent = (props) => {
	const { overviewData, selectedRangeValue, tab_name, tab_id } = props;

	// basic dd for column chart values
	const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0] : null;
	const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

	// Data Distribution
	const overall = overviewData ? overviewData.overall : [];
	const segment_revenue_mix = overviewData ? overviewData.segment_revenue_mix : [];
	const segment_details = overviewData ? overviewData.segment_details : [];
	const new_logos_by_segment = overviewData ? overviewData.new_logos_by_segment : [];

	const new_logos_by_segment_in_quarter_number = overviewData ? overviewData.new_logos_by_segment_in_quarter_number : [];
	const new_logos_by_segment_in_quarter_percentage = overviewData ? overviewData.new_logos_by_segment_in_quarter_percentage : [];
	const new_logos_by_segment_in_year_number = overviewData ? overviewData.new_logos_by_segment_in_year_number : [];
	const new_logos_by_segment_in_year_percentage = overviewData ? overviewData.new_logos_by_segment_in_year_percentage : [];

	const [kpiClicked, setkpiClicked] = useState(null);

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const segmentFormat = (cell, row) => {
		return formatLongText(cell, row)
	};

	const defaultSorted = [{
		dataField: 'segment',
		order: 'desc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',
			sort: true,
			formatter: segmentFormat
		},
		{
			dataField: 'new_logos_count',
			text: 'NEW LOGOS',
			sort: true,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'mix_percent',
			text: 'MIX %',
			sort: true,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'q_by_q_growth_percent',
			text: 'Q/Q % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'q_to_q_number_growth',
			text: 'Q-Q # GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'y_by_y_growth_percent',
			text: 'Y/Y % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'y_to_y_number_growth',
			text: 'Y-Y # GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right'
		},
	];

	return (
		<>
			<Row className="contentDivider pb-30">
				<div className="fix-width">
					<Card className="revenue" >
						<Card.Body>
							<Card.Title>{tab_name}</Card.Title>
							<KPI_NEW
								totalValue={overall.total.value}
								totalUnit={overall.total.unit}
								details={overall.details}
								setkpiClicked={setkpiClicked}
								kpiClicked={kpiClicked}
							/>
						</Card.Body>
					</Card>
				</div>
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>	{kpiClicked ? kpiClicked : tab_name} BY SEGMENT	</Card.Title>
							{kpiClicked === '% of MPQ' ?
								<>
									<Legend legend={new_logos_by_segment_in_quarter_percentage.legend} />
									<div style={{ height: "400px" }}>
										<LineChart
											id={`${tab_id}-new_logos_by_segment_in_quarter_percentage-line_chart-segment`}
											data={new_logos_by_segment_in_quarter_percentage.data}
											legend={new_logos_by_segment_in_quarter_percentage.legend}
											isAnimationEnabled={false}
										/>
									</div>
								</>
								:

								kpiClicked === '% of Budget' ?
									<>
										<Legend legend={new_logos_by_segment_in_quarter_number.legend} />
										<div style={{ height: "400px" }}>
											<LineChart
												id={`${tab_id}-new_logos_by_segment_in_quarter_number-line_chart-segment`}
												data={new_logos_by_segment_in_quarter_number.data}
												legend={new_logos_by_segment_in_quarter_number.legend}
												isAnimationEnabled={false}
											/>
										</div>
									</>
									:
									kpiClicked === 'Q/Q Growth %' ?
										<>
											<Legend legend={new_logos_by_segment_in_year_number.legend} />
											<div style={{ height: "400px" }}>
												<LineChart
													id={`${tab_id}-new_logos_by_segment_in_year_number-line_chart-segment`}
													data={new_logos_by_segment_in_year_number.data}
													legend={new_logos_by_segment_in_year_number.legend}
													isAnimationEnabled={false}
												/>
											</div>
										</>
										:
										kpiClicked === 'Y/Y Growth %' ?
											<>
												<Legend legend={new_logos_by_segment_in_year_percentage.legend} />
												<div style={{ height: "400px" }}>
													<LineChart
														id={`${tab_id}-new_logos_by_segment_in_year_percentage-line_chart-segment`}
														data={new_logos_by_segment_in_year_percentage.data}
														legend={new_logos_by_segment_in_year_percentage.legend}
														isAnimationEnabled={false}
													/>
												</div>
											</>
											:
											<>
												<div className="d-flex">
													<Legend legend={new_logos_by_segment.legend} />
													{selectedRangeValue === "last_completed_quarter" ?
														<BasicDD
															className="ml-auto"
															id={`${tab_id}-stacked_column_dd`}
															options={optionOverviewFilter}
															value={stackedColumnSelection}
															setValue={setStackedColumnSelection}
														/> : null
													}
												</div>
												<div style={{ height: "400px", marginTop: "1rem" }}>
													<StackedColumnChart
														id={`${tab_id}-stacked_column_chart`}
														hiddenValueAxis={false}
														isOneHundredPercent={false}
														isPercentage={false}
														largerWidth={true}
														hideLabelBullet={false}
														isOverviewChart={true}
														isGrowthPercentToggable={true}
														stackedColumnSelection={stackedColumnSelection.key}
														selectedRangeValue={selectedRangeValue}
														data={new_logos_by_segment.data}
														legend={new_logos_by_segment.legend}
														isAnimationEnabled={false}
													/>
												</div>
											</>
							}

						</Card.Body>
					</Card>
				</Col>
				<Col style={{ maxWidth: "400px" }}>
					<Card className="Segment revenue">
						<Card.Body>
							<Card.Title>{tab_name} MIX % BY SEGMENT</Card.Title>
							<Legend legend={segment_revenue_mix.legend} />
							<div style={{ height: "350px" }}>
								<DonutChart
									id={`${tab_id}-donut_chart`}
									totalTitle={tab_name}
									total={segment_revenue_mix.cross_border_bkgs}
									data={segment_revenue_mix.data}
									legend={segment_revenue_mix.legend}
									isDollar={true}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider">
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>SEGMENT DETAILS</Card.Title>
							<CustomTable
								tableName='segment-details scrollable'
								data={segment_details ? segment_details : []}
								defaultSorted={defaultSorted}
								columns={columns}
							/>
						</Card.Body>
					</Card>
				</Col>
			</Row>
		</>
	);
};

export default OverviewContent;